.carousel {
  width: 100%;
  height: 300px;
  transition: transform 0.3s;
  display: flex;
  flex-flow: row;
}
.carousel .image-container {
  position: relative;
  min-width: 100%;
  height: 290px;
}
.carousel .image-container img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}/*# sourceMappingURL=CarouselItem.css.map */