@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");
body {
  padding: 0;
  margin: 0;
  background-color: white;
  color: black;
}

* {
  margin: 0;
  padding: 0;
}

.grey {
  background-color: #999999;
}

.green {
  background-color: green;
}

.red {
  background-color: red;
}

.main-wrap {
  position: relative;
  margin: 0 auto;
  width: 100%;
  z-index: 1999;
  overflow: hidden;
  background-color: #130d0c;
}
.main-wrap h1 {
  font-size: 52px;
}
.main-wrap h2 {
  font-size: 36px;
}
.main-wrap h3 {
  font-size: 32px;
  font-weight: 200;
}
.main-wrap h4 {
  font-size: 28px;
}
.main-wrap p {
  font-size: 18px;
}
.main-wrap h1,
.main-wrap h2,
.main-wrap h3,
.main-wrap h4,
.main-wrap p {
  line-height: 130%;
}
.main-wrap .text-center {
  width: 100%;
  text-align: center;
}
.main-wrap .box-shadow {
  filter: drop-shadow(10px 10px 10px rgba(0, 0, 0, 0.2));
}
.main-wrap p,
.main-wrap a {
  font-family: "Inter", sans-serif;
  font-weight: 200;
  font-style: normal;
  font-size: 18px;
  letter-spacing: 0.1px;
}
.main-wrap h1,
.main-wrap h2,
.main-wrap h3,
.main-wrap h4 {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  line-height: 130%;
  letter-spacing: 2px;
}
.main-wrap h3 {
  font-weight: 200;
}
.main-wrap .text-white {
  color: white;
}
.main-wrap .section {
  background-color: white;
  width: 1280px;
  padding: 0;
  position: relative;
  margin: 0 auto;
  position: relative;
  align-items: center;
  display: flex;
  flex-flow: row wrap;
  z-index: 1;
}
.main-wrap .section .wrapper {
  display: flex;
}
.main-wrap .section .wrapper .row {
  display: flex;
  flex-flow: row;
}
.main-wrap .section .wrapper .col {
  display: flex;
  flex-flow: row wrap;
  flex: 0 0 auto;
  position: relative;
  align-items: start;
  justify-items: center;
  justify-content: flex-start;
}
.main-wrap .section .wrapper .col.col-wrapper {
  padding: 0;
}
.main-wrap .section .wrapper .col.col-0 {
  flex: 0 1 0%;
}
.main-wrap .section .wrapper .col.col-1 {
  flex: 0 1 8.3333333333%;
}
.main-wrap .section .wrapper .col.col-2 {
  flex: 0 1 16.6666666667%;
}
.main-wrap .section .wrapper .col.col-3 {
  flex: 0 1 25%;
}
.main-wrap .section .wrapper .col.col-4 {
  flex: 0 1 33.3333333333%;
}
.main-wrap .section .wrapper .col.col-5 {
  flex: 0 1 41.6666666667%;
}
.main-wrap .section .wrapper .col.col-6 {
  flex: 0 1 50%;
}
.main-wrap .section .wrapper .col.col-7 {
  flex: 0 1 58.3333333333%;
}
.main-wrap .section .wrapper .col.col-8 {
  flex: 0 1 66.6666666667%;
}
.main-wrap .section .wrapper .col.col-9 {
  flex: 0 1 75%;
}
.main-wrap .section .wrapper .col.col-10 {
  flex: 0 1 83.3333333333%;
}
.main-wrap .section .wrapper .col.col-11 {
  flex: 0 1 91.6666666667%;
}
.main-wrap .section .wrapper .col.col-12 {
  flex: 0 1 100%;
}
.main-wrap .section .wrapper .col.col-space-between {
  justify-content: space-between;
}
.main-wrap .section .hover-overlay {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.144);
  opacity: 0;
}
.main-wrap .section .hover-element {
  position: relative;
  display: inline-block;
}
.main-wrap .section .background-container {
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.15);
  border-radius: 3px;
