.section.section-navbar {
  position: fixed;
  height: 100px;
  max-width: 1280px;
  width: 100%;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  background-color: #2b3034;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.section.section-navbar .navbar-container {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  width: 100%;
  padding: 18px 100px 0;
}
.section.section-navbar .navbar-container .logo-container {
  margin-top: 2px;
  height: 44px;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
}
.section.section-navbar .navbar-container .navbar-list {
  display: flex;
  flex-direction: column;
  align-items: end;
}
.section.section-navbar .navbar-container .navbar-list .navbar-item {
  margin-right: 0;
  margin-bottom: 11px;
}
.section.section-navbar .navbar-container .navbar-list .navbar-item a {
  text-decoration: none;
